<script>
import { Swipe, SwipeItem } from 'vant'
import swipeList, { originalConfig, codeMap } from './config'
import { getSearch } from '@/utils'
import wxAuth from '@/helper/Auth/wxAuth'
import { mapState } from 'vuex'
export default {
  components: {
    VanSwipe: Swipe,
    VanSwipeItem: SwipeItem
  },
  data () {
    return {
      stateIsShow: getSearch('medId') === '1610', // 1610 快手渠道
      stateModelShow: false, // 声明弹窗 快手渠道需要显示此弹窗
      ...originalConfig, // 埋点数据
      swipeList, // 轮播数组
      phone: '', // 手机号
      code: '', // 验证码
      paytype: 1 // 支付类型
    }
  },
  async beforeRouteEnter (to, from, next) {
    // 获取微信授权
    await wxAuth({
      getTokenQuery: { ...getSearch() }
    })
    next()
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  async created () {
    const {
      id,
      unionid,
      wx_body: { openid }
    } = this.userInfo
    this.$log.update({
      openid,
      unionid,
      uid: id,
      mid: id
    })
    this.checkOrderInWx()
  },
  methods: {
    checkOrderInWx () {
      return this.$apis.payment
        .checkOrderInWx({ product_sn: 'PRODUCT_SN_XIAOBAI_12' })
        .catch((err) => {
          this.payErrorBack(err)
          return Promise.reject(err)
        })
    },
    payErrorBack (data) {
      console.log(data)
      if (data.code === 30009) {
        this.$dialog({
          message: '您已学习过此课程',
          confirmButtonText: '查看老师'
        }).then(() => {
          this.$log.click(codeMap[data.code].report)
          this.$router.push({
            name: 'BaiduPcTeacherQrCode'
          })
        })
        return
      }
      if (codeMap[data.code]) {
        this.$dialog({ message: data.msg || codeMap[data.code].msg }).then(() => {
          this.$log.click(codeMap[data.code].report)
        })
      }
    },
    async pay () {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_toufang_lcx_0386'
      })
      this.$apis.userOperator.report()
      this.$apis.payment.payInWx({
        pay_type: 2,
        pay_mode: 3,
        ...getSearch()
      })
        .then(result => {
          window.WeixinJSBridge.invoke('getBrandWCPayRequest', result, (res) => {
            if (res.err_msg === 'get_brand_wcpay_request:ok') {
              this.$router.push({
                name: 'BaiduPcTeacherQrCode'
              })
            }
          })
        })
        .catch(err => {
          this.payErrorBack(err)
        })
    }
  }
}
</script>
